//  
export const fileService = {
  //  
  uploadFile: async (
    filePath: string,
    file: File,
    metadata: { userId: string, type: string }
  ): Promise<FileAttachment> => {
    //    Supabase Storage
    const { data: fileData, error: uploadError } = await supabaseClient.storage
      .from('files')
      .upload(filePath, file, {
        upsert: true
      });
    
    if (uploadError) throw uploadError;
    
    //   URL 
    const { data: urlData } = supabaseClient.storage
      .from('files')
      .getPublicUrl(filePath);
    
    //      
    const { data: attachment, error: insertError } = await supabaseClient
      .from('file_attachments')
      .insert([
        {
          owner_id: metadata.userId,
          name: file.name,
          type: metadata.type,
          size: file.size,
          url: urlData.publicUrl,
          created_at: new Date().toISOString()
        }
      ])
      .select()
      .single();
    
    if (insertError) throw insertError;
    
    return attachment;
  },
  
  //  
  deleteFile: async (fileId: string, filePath: string) => {
    //    
    const { error: deleteStorageError } = await supabaseClient.storage
      .from('files')
      .remove([filePath]);
    
    if (deleteStorageError) throw deleteStorageError;
    
    //      
    const { error: deleteRecordError } = await supabaseClient
      .from('file_attachments')
      .delete()
      .eq('id', fileId);
    
    if (deleteRecordError) throw deleteRecordError;
    
    return true;
  },
  
  //    
  getUserFiles: async (userId: string) => {
    const { data, error } = await supabaseClient
      .from('file_attachments')
      .select('*')
      .eq('owner_id', userId);
    
    if (error) throw error;
    return data;
  }
};

//  
export default {
  auth: authService,
  surveyors: surveyorService,
  companies: companyService,
  projects: projectService,
  files: fileService
}; 